import React , { useState, useEffect } from 'react';
import styles from '../../styles/Detail.module.css';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';
import { FaTrash } from "react-icons/fa";


import Detail from '../Detail';
import {get} from '../../Fonctions/requets';
import config from '../../Fonctions/config';


const DetailEmployee= ({ id,setVisibility,mode,setDonnees}) => {
    const [infos, setInfos] = useState({});
    const MySwal = withReactContent(Swal);
    useEffect(() => {
        const fetchData = async () => {
            const data = await get(config.URL+`/api/employees/${id}/`);
            if (!data) {
                setInfos({});
                return;
            }
            setInfos(data);
        }
        if (mode === "Ajout")
            return;
        fetchData();
    }, [id,mode]);

    const [formations, setFormations] = useState([]);
    useEffect(() => {
        const fetchFormations = async () => {
            const data = await get(config.URL+`/api/employees/${id}/formations/`);
            if (!data) {
                setFormations([]);
                return;
            }
            setFormations(data);
        }
        if (mode === "Ajout")
            return;
        fetchFormations();
    }, [id,mode]);

    const [paramtrage, setParamtrage] = useState([]);
    useEffect(() => {
        let param = [
            { label: 'Identifiant', value: infos.login, type: 'input' },
            { label: 'Nom', value: infos.lastname, type: 'input' },
            { label: 'Prnom', value: infos.name, type: 'input' },
            { label: 'Contrat', value: infos.contrat, type: 'input' },
            { label: 'Carte Btp', value: infos.btp_card, type: 'image' },
            { label: 'Numro de tlphone', value: infos.telephone, type: 'input' },
        ];
        if (mode === "Ajout") {
            param.push({ label: 'Mot de passe', value: '', type: 'input' });
        }

        setParamtrage(param);
    }, [infos,mode]);

    const [formationsList, setFormationsList] = useState([]);
    useEffect(() => {
        const fetchFormationsList = async () => {
            const data = await get(config.URL+`/api/formations/`);
            if (!data) {
                setFormationsList([]);
                return;
            }
            setFormationsList(data.filter(formation => !formations.some(f => f.id === formation.id)));
        }
        fetchFormationsList();
    }, [formations]);


    const handleSave = async (values) => {
        const formdata = new FormData();
        formdata.append('login', values['Identifiant']);
        formdata.append('name', values['Prnom']);
        formdata.append('lastname', values['Nom']);
        formdata.append('contrat', values['Contrat']);
        formdata.append('telephone', values['Numro de tlphone']);
        if (values['Carte Btp'] && values['Carte Btp'] instanceof File) {
            formdata.append('btp_card', values['Carte Btp']);
        }

        const response = await fetch(config.URL+`/api/employees/${id}/`, {
            method: 'PATCH',
            headers : {
                'Authorization': `Bearer ${localStorage.getItem('access')}`,
            },
            body: formdata,
        });
        if (response.ok) {
            const data = await response.json();
            setDonnees((prev) => {
                const newDonnees = [...prev];
                const index = newDonnees.findIndex(item => item.title === 'id');
                if (index !== -1) {
                    newDonnees[index].contenu = newDonnees[index].contenu.map(item => item === id ? data.id : item);
                }
                const index2 = newDonnees.findIndex(item => item.title === 'Identifiant');
                if (index2 !== -1) {
                    newDonnees[index2].contenu = newDonnees[index2].contenu.map(item => item === infos.login ? data.login : item);
                }
                const index3 = newDonnees.findIndex(item => item.title === 'Nom');
                if (index3 !== -1) {
                    newDonnees[index3].contenu = newDonnees[index3].contenu.map(item => item === infos.lastname ? data.lastname : item);
                }
                const index4 = newDonnees.findIndex(item => item.title === 'Prnom');
                if (index4 !== -1) {
                    newDonnees[index4].contenu = newDonnees[index4].contenu.map(item => item === infos.name ? data.name : item);
                }
                const index5 = newDonnees.findIndex(item => item.title === 'Contrat');
                if (index5 !== -1) {
                    newDonnees[index5].contenu = newDonnees[index5].contenu.map(item => item === infos.contrat ? data.contrat : item);
                }
                const index6 = newDonnees.findIndex(item => item.title === 'Numro de tlphone');
                if (index6 !== -1) {
                    newDonnees[index6].contenu = newDonnees[index6].contenu.map(item => item === infos.telephone ? data.telephone : item);
                }
                return newDonnees;
            });

            MySwal.fire({
                title: <strong>Employ modifi avec succs</strong>,
                icon: 'success',
            });
            setVisibility(false);
        } else {
            MySwal.fire({
                title: <strong>Erreur lors de la modification</strong>,
                icon: 'error',
            });
        }
    }

    const handleDelete = async () => {
        const result = await MySwal.fire({
            title: <strong>tes-vous sr de vouloir supprimer cet employ ?</strong>,
            html: <i>Cette action supprimera toutes les donnes lies (Formations, prsences,photos envoyes, etc...)</i>,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Oui',
            cancelButtonText: 'Non',
        });
        if (result.isConfirmed) {
            const response = await fetch(config.URL+`/api/employees/${id}/`, {
                method: 'DELETE',
                headers : {
                    'Authorization': `Bearer ${localStorage.getItem('access')}`,
                    'Content-Type': 'application/json',
                },
            });
            if (response.ok) {
                setDonnees((prev) => {
                    const newDonnees = [...prev];
                    const index = newDonnees.findIndex(item => item.title === 'id');
                    if (index !== -1) {
                        newDonnees[index].contenu = newDonnees[index].contenu.filter(item => item !== id);
                    }
                    return newDonnees;
                });

                MySwal.fire({
                    title: <strong>Employ supprim avec succs</strong>,
                    icon: 'success',
                });
                setVisibility(false);
            }
            else {
                MySwal.fire({
                    title: <strong>Erreur lors de la suppression</strong>,
                    icon: 'error',
                });
            }
        }
        else{
            MySwal.fire({
                title: <strong>Suppression annule</strong>,
                icon: 'info',
            });
        }
    }

    const handleAdd = async (values) => {
        if (!values['Identifiant'] || !values['Nom'] || !values['Prnom'] || !values['Contrat'] || !values['Mot de passe'] ) {
            MySwal.fire({
                title: <strong>Erreur</strong>,
                html: <i>Les champs Identifiant, Nom, Prnom, Contrat et Mot de passe sont obligatoires.</i>,
                icon: 'error',
            });
            return;
        }
        const formData = new FormData();
        formData.append('login', values['Identifiant']);
        formData.append('name', values['Prnom']);
        formData.append('lastname', values['Nom']);
        formData.append('contrat', values['Contrat']);
        formData.append('password', values['Mot de passe']);
        formData.append('is_active', true);

        if (values['Carte Btp'] && values['Carte Btp'] instanceof File)
            formData.append('btp_card', values['Carte Btp']);

        if (values['Numro de tlphone'])
            formData.append('telephone', values['Numro de tlphone']);


        const response = await fetch(config.URL+`/api/employees/`, {
            method: 'POST',
            headers : {
                'Authorization': `Bearer ${localStorage.getItem('access')}`,
            },
            body: formData,
        });
        if (response.ok) {
            const data = await response.json();
            console.log(data);
            setDonnees((prev) => {
                const isExisting = prev.some(item => item.contenu.includes(data.id));
                if (isExisting) {
                    return prev;
                }
                const newDonnees = [...prev];
                const index = newDonnees.findIndex(item => item.title === 'id');
                if (index !== -1) {
                    newDonnees[index].contenu = [...newDonnees[index].contenu, data.id];
                }
                const index2 = newDonnees.findIndex(item => item.title === 'Identifiant');
                if (index2 !== -1) {
                    newDonnees[index2].contenu = [...newDonnees[index2].contenu, data.login];
                }
                const index3 = newDonnees.findIndex(item => item.title === 'Nom');
                if (index3 !== -1) {
                    newDonnees[index3].contenu = [...newDonnees[index3].contenu, data.lastname];
                }
                const index4 = newDonnees.findIndex(item => item.title === 'Prnom');
                if (index4 !== -1) {
                    newDonnees[index4].contenu = [...newDonnees[index4].contenu, data.name];
                }
                const index5 = newDonnees.findIndex(item => item.title === 'Contrat');
                if (index5 !== -1) {
                    newDonnees[index5].contenu = [...newDonnees[index5].contenu, data.contrat];
                }
                const index6 = newDonnees.findIndex(item => item.title === 'Numro de tlphone');
                if (index6 !== -1) {
                    newDonnees[index6].contenu = [...newDonnees[index6].contenu, data.telephone];
                }
                return newDonnees;
            });
            MySwal.fire({
                title: <strong>Employ ajout avec succs</strong>,
                icon: 'success',
            });
            setVisibility(false);
        } else {
            MySwal.fire({
                title: <strong>Erreur lors de l'ajout</strong>,
                icon: 'error',
            });
        }
    }

    const handleAddFormation = async (e) => {
        const selectedId = e.target.value;
        const selectedFormation = formationsList.find(formation => formation.id.toString() === selectedId.toString());

        if (selectedFormation) {
            const newFormation = {
                id: selectedFormation.id,
                name: selectedFormation.name,
                file: selectedFormation.file,
            };
            const response = await fetch(config.URL+`/api/employees/${id}/`, {
                method: 'PATCH',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('access')}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    formations: [...formations.map(f => f.id), newFormation.id],
                }),
            });
            if (response.ok) {
                MySwal.fire({
                    title: <strong>Formation ajoute avec succs</strong>,
                    icon: 'success',
                });
                setFormations([...formations, newFormation]);
                setFormationsList(formationsList.filter(formation => formation.id.toString() !== selectedId.toString()));
            }else {
                MySwal.fire({
                    title: <strong>Erreur lors de l'ajout de la formation</strong>,
                    icon: 'error',
                });
            }
            e.target.value = "";
        }
    }

    const handleDeleteFormation = async (formationId) => {
        const result = await MySwal.fire({
            title: <strong>tes-vous sr de vouloir supprimer cette formation ?</strong>,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Oui',
            cancelButtonText: 'Non',
        });
        if (result.isConfirmed) {
            const response = await fetch(config.URL+`/api/employees/${id}/`, {
                method: 'PATCH',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('access')}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    formations: formations.filter(formation => formation.id !== formationId).map(f => f.id),
                }),
            });
            if (response.ok) {
                MySwal.fire({
                    title: <strong>Formation supprime avec succs</strong>,
                    icon: 'success',
                });
                setFormations(formations.filter(formation => formation.id !== formationId));
                const deletedFormation = formations.find(formation => formation.id === formationId);
                setFormationsList([...formationsList, deletedFormation]);
            } else {
                MySwal.fire({
                    title: <strong>Erreur lors de la suppression de la formation</strong>,
                    icon: 'error',
                });
            }
        }
        else{
            MySwal.fire({
                title: <strong>Suppression annule</strong>,
                icon: 'info',
            });
        }
    }

    const ReinitialisePassword = async () => {
        const result = await MySwal.fire({
            title: <strong>tes-vous sr de vouloir rinitialiser le mot de passe ?</strong>,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Oui',
            cancelButtonText: 'Non',
        });
        if (result.isConfirmed){
            const new_password = await MySwal.fire({
                title: <strong>Entrez le nouveau mot de passe</strong>,
                input: 'password',
                inputAttributes: {
                    autocapitalize: 'off',
                    autocorrect: 'off'
                },
                showCancelButton: true,
                confirmButtonText: 'Rinitialiser',
                showLoaderOnConfirm: true,
                preConfirm: (password) => {
                    if (!password) {
                        MySwal.showValidationMessage('Veuillez entrer un mot de passe');
                    }
                    return password;
                },
                allowOutsideClick: () => !MySwal.isLoading()
            });
            if (new_password.isConfirmed) {
                const response = await fetch(config.URL+`/api/employees/${id}/`, {
                    method: 'PATCH',
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('access')}`,
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify({
                        password: new_password.value,
                    }),
                });
                if (response.ok) {
                    MySwal.fire({
                        title: <strong>Mot de passe rinitialis avec succs</strong>,
                        icon: 'success',
                    });
                } else {
                    MySwal.fire({
                        title: <strong>Erreur lors de la rinitialisation du mot de passe</strong>,
                        icon: 'error',
                    });
                }
            }
        }
    }


    if (mode === "Ajout") {
        return (
            <div style={{ display: 'flex', flexDirection: 'column', gap: '10px',width:'100%',maxHeight:'100vh',overflow:'scroll',justifyContent:'center',alignItems:'center' }}>
                <Detail paramtrage={paramtrage} mode={mode} onSave={handleSave} onDelete={handleDelete} onAdd={handleAdd} onCancel={() => setVisibility(false)} />
            </div>
        );
    }

    return (
        <div style={{ display: 'flex', flexDirection: 'column', gap: '10px',width:'100%',maxHeight:'100vh',overflow:'scroll',justifyContent:'center',alignItems:'center' }}>
            <Detail paramtrage={paramtrage} mode={mode} onSave={handleSave} onDelete={handleDelete} onAdd={handleAdd} onCancel={() => setVisibility(false)} />
            <button 
                onClick={ReinitialisePassword} 
                style={{
                    marginTop:10,
                    backgroundColor:'#52bdf1',
                    color:'white',
                    border:'none',
                    borderRadius:5,
                    cursor:'pointer',
                    fontSize:16,
                    fontWeight:'bold',
                    padding: 20
                }}
            >
                Reinitialiser le mot de passe
            </button>
            <div className={styles.card} style={{maxWidth:'50%'}}>
                <div className={styles.title}>
                    Formations
                </div>
                <select 
                    className={styles.select} 
                    defaultValue=""
                    onChange={handleAddFormation}
                >
                    <option value="" disabled>--Ajouter une formation--</option>
                    {formationsList.map((formation, index) => (
                        <option key={index} value={formation.id}>{formation.name}</option>
                    ))}
                </select>
                {formations.map((formations, index) => (
                    <div key={index}>
                        <a href={`${formations.file}`} target="_blank" rel="noopener noreferrer">
                            {formations.name}
                        </a>
                        <button 
                        onClick={() => handleDeleteFormation(formations.id)}
                        style={{
                            marginLeft:10,
                            backgroundColor:'red',
                            color:'white',
                            border:'none',
                            borderRadius:5,
                            padding:3,
                            cursor:'pointer',
                            fontSize:12,
                        }}>
                            <FaTrash />
                        </button>
                    </div>
                ))}
            </div>
        </div> 
    );
}

export default DetailEmployee;