import React , { useState, useEffect } from 'react';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';

import Detail from '../Detail';
import {get} from '../../Fonctions/requets';
import config from '../../Fonctions/config';

const DetailStatusTravail = ({ id,setVisibility,mode,setDonnees }) => {
    const [infos, setInfos] = useState({});
    const MySwal = withReactContent(Swal);

    useEffect(() => {
        const fetchData = async () => {
            const data = await get(config.URL+`/api/status_travail/${id}/`);
            if (!data) {
                setInfos({});
                return;
            }
            setInfos(data);
        }
        if (mode === "Ajout")
            return;
        fetchData();
    }, [id,mode]);

    const [paramtrage, setParamtrage] = useState([]);
    useEffect(() => {
        setParamtrage([
            { label: 'Nom', value: infos.name, type: 'input' },
            { label: 'Raccourcis', value: infos.raccourcis, type: 'input' },
        ]);
    }
    , [infos]);

    const handleSave = async (values) => {
        try {
            const response = await fetch(config.URL+`/api/status_travail/${id}/`, {
                method: 'PATCH',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('access')}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    name: values['Nom'],
                    raccourcis: values['Raccourcis'],
                }),
            });
            if (response.ok){
                const data = await response.json();
                setDonnees((prev) => {
                    const newDonnees = [...prev];
                    const index = newDonnees.findIndex(item => item.title === 'id');
                    if (index !== -1) {
                        newDonnees[index].contenu = newDonnees[index].contenu.map(item => item === id ? data.id : item);
                    }
                    const index2 = newDonnees.findIndex(item => item.title === 'Nom');
                    if (index2 !== -1) {
                        newDonnees[index2].contenu = newDonnees[index2].contenu.map(item => item === infos.name ? data.name : item);
                    }
                    const index3 = newDonnees.findIndex(item => item.title === 'Raccourcis');
                    if (index3 !== -1) {
                        newDonnees[index3].contenu = newDonnees[index3].contenu.map(item => item === infos.raccourcis ? data.raccourcis : item);
                    }
                    return newDonnees;
                });
                MySwal.fire({
                    title: <strong>Status modifi avec succs</strong>,
                    icon: 'success',
                });
            } else {
                MySwal.fire({
                    title: <strong>Erreur lors de la modification</strong>,
                    html: '<i>Une erreur inconnue est survenue.</i>',
                    icon: 'error',
                    confirmButtonText: 'OK',
                });
            }
        } catch (error) {
            MySwal.fire({
                title: <strong>Erreur</strong>,
                html: <i>Une erreur inconnue est survenue.</i>,
                icon: 'error',
            });
        }
        finally {
            setVisibility(false);
        }
    }

    const handleDelete = async (values) => {
        const result = await MySwal.fire({
            title: <strong>tes-vous sr de vouloir supprimer ce status ?</strong>,
            html: <i>Cette action supprimera toutes les donnes lies (Prsences, etc...)</i>,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Oui',
            cancelButtonText: 'Non',
        });
        if (result.isConfirmed){
            try {
                const response = await fetch(config.URL+`/api/status_travail/${id}/`, {
                    method: 'DELETE',
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('access')}`,
                        'Content-Type': 'application/json',
                    },
                });
                if (response.ok) {
                    setDonnees((prev) => {
                        const newDonnees = [...prev];
                        const index = newDonnees.findIndex(item => item.title === 'id');
                        if (index !== -1) {
                            newDonnees[index].contenu = newDonnees[index].contenu.filter(item => item !== id);
                        }
                        return newDonnees;
                    });
                    MySwal.fire({
                        title: <strong>Status supprim avec succs</strong>,
                        icon: 'success',
                    });
                } else  {
                    MySwal.fire({
                        title: <strong>Erreur lors de la suppression</strong>,
                        html: '<i>Une erreur inconnue est survenue.</i>',
                        icon: 'error',
                        confirmButtonText: 'OK',
                    });
                }
            } catch (error) {
                MySwal.fire({
                    title: <strong>Erreur</strong>,
                    html: <i>Une erreur inconnue est survenue.</i>,
                    icon: 'error',
                });
            }
            finally {
                setVisibility(false);
            }
        }
        else {
            MySwal.fire({
                title: <strong>Suppression annule</strong>,
                icon: 'info',
            });
        }
    }

    const handleAdd = async (values) => {
        if (!values['Nom'] || !values['Raccourcis']) {
            MySwal.fire({
                title: <strong>Erreur</strong>,
                html: <i>Le nom et le raccourcis doivent tre prsents</i>,
                icon: 'warning',
            });
            return;
        }
        try {
            const response = await fetch(config.URL+`/api/status_travail/`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('access')}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    name: values['Nom'],
                    raccourcis: values['Raccourcis'],
                }),
            });
            if (response.ok) {
                const data = await response.json();
                setDonnees((prev) => {
                    const isExisting = prev.some(item => item.contenu.includes(data.id));
                    if (isExisting) {
                        return prev;
                    }
                    const newDonnees = [...prev];
                    const index = newDonnees.findIndex(item => item.title === 'id');
                    if (index !== -1) {
                        newDonnees[index].contenu.push(data.id);
                    }
                    const index2 = newDonnees.findIndex(item => item.title === 'Nom');
                    if (index2 !== -1) {
                        newDonnees[index2].contenu.push(data.name);
                    }
                    const index3 = newDonnees.findIndex(item => item.title === 'Raccourcis');
                    if (index3 !== -1) {
                        newDonnees[index3].contenu.push(data.raccourcis);
                    }
                    return newDonnees;
                });
                MySwal.fire({
                    title: <strong>Status ajout avec succs</strong>,
                    icon: 'success',
                });
            } else {
                MySwal.fire({
                    title: <strong>Erreur lors de l'ajout</strong>,
                    html: '<i>Une erreur inconnue est survenue.</i>',
                    icon: 'error',
                    confirmButtonText: 'OK',
                });
            }
        } catch (error) {
            MySwal.fire({
                title: <strong>Erreur</strong>,
                html: <i>Une erreur inconnue est survenue.</i>,
                icon: 'error',
            });
        }
        finally {
            setVisibility(false);
        }
    }
    return (
        <div style={{ display: 'flex', flexDirection: 'column', gap: '10px',width:'100%',maxHeight:'100vh',overflow:'scroll',justifyContent:'center',alignItems:'center' }}>
            <Detail paramtrage={paramtrage} mode={mode} onSave={handleSave} onDelete={handleDelete} onAdd={handleAdd} onCancel={() => setVisibility(false)} />
        </div>
    );
}

export default DetailStatusTravail;
