import React , { useState, useEffect } from 'react';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';

import Detail from '../Detail';
import {get} from '../../Fonctions/requets';
import config from '../../Fonctions/config';


const DetailZones = ({ id,setVisibility,mode,setDonnees }) => {
    const [infos, setInfos] = useState({});
    const MySwal = withReactContent(Swal);

    useEffect(() => {
        const fetchData = async () => {
            const data = await get(config.URL+`/api/zones/${id}/`);
            if (!data) {
                setInfos({});
                return;
            }
            setInfos(data);
        }
        if (mode === "Ajout")
            return;
        fetchData();
    }, [id,mode]);

    const [paramtrage, setParamtrage] = useState([]);
    useEffect(() => {
        setParamtrage([
            { label: 'Ville', value: infos.villes, type: 'input' },
            { label: 'Dept', value: infos.dept, type: 'input' },
            { label: 'Zone', value: infos.zone, type: 'input' },
            { label: 'Km', value: infos.km, type: 'input' },
        ]);
    }, [infos]);

    const handleSave = async (values) => {
        try {
            const response = await fetch(config.URL+`/api/zones/${id}/`, {
                method: 'PATCH',
                headers : {
                    'Authorization': `Bearer ${localStorage.getItem('access')}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    villes: values['Ville'],
                    dept: values['Dept'],
                    zone: values['Zone'],
                    km: values['Km'],
                }),
            });
            if (response.ok) {
                const data = await response.json();
                setDonnees((prev) => {
                    const newDonnees = [...prev];
                    const index = newDonnees.findIndex(item => item.title === 'id');
                    if (index !== -1) {
                        newDonnees[index].contenu = newDonnees[index].contenu.map(item => item === id ? data.id : item);
                    }
                    const index2 = newDonnees.findIndex(item => item.title === 'Ville');
                    if (index2 !== -1) {
                        newDonnees[index2].contenu = newDonnees[index2].contenu.map(item => item === infos.villes ? data.villes : item);
                    }
                    const index3 = newDonnees.findIndex(item => item.title === 'Dept');
                    if (index3 !== -1) {
                        newDonnees[index3].contenu = newDonnees[index3].contenu.map(item => item === infos.dept ? data.dept : item);
                    }
                    const index4 = newDonnees.findIndex(item => item.title === 'Zones');
                    if (index4 !== -1) {
                        newDonnees[index4].contenu = newDonnees[index4].contenu.map(item => item === infos.zone ? data.zone : item);
                    }
                    const index5 = newDonnees.findIndex(item => item.title === 'Kms');
                    if (index5 !== -1) {
                        newDonnees[index5].contenu = newDonnees[index5].contenu.map(item => item === infos.km ? data.km : item);
                    }
                    return newDonnees;
                });
                MySwal.fire({
                    title: <strong>Zone modifie avec succs</strong>,
                    icon: 'success',
                });
            } else {
                MySwal.fire({
                    title: <strong>Erreur lors de la modification</strong>,
                    icon: 'error',
                });
            }
        }
        catch (error) {
            MySwal.fire({
                title: <strong>Erreur lors de la modification</strong>,
                icon: 'error',
            });
        }
        finally {
            setVisibility(false);
        }
    }

    const handleDelete = async () => {
        const result = await MySwal.fire({
            title: <strong>tes-vous sr de vouloir supprimer cette zone ?</strong>,
            html: <i>Cette action supprimera toutes les donnes lies (chantiers, etc...)</i>,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Oui',
            cancelButtonText: 'Non',
        });
        if (result.isConfirmed) {
            try {
                const response = await fetch(config.URL+`/api/zones/${id}/`, {
                    method: 'DELETE',
                    headers : {
                        'Authorization': `Bearer ${localStorage.getItem('access')}`,
                        'Content-Type': 'application/json',
                    },
                });
                if (response.ok) {
                    setDonnees((prev) => {
                        const newDonnees = [...prev];
                        const index = newDonnees.findIndex(item => item.title === 'id');
                        if (index !== -1) {
                            newDonnees[index].contenu = newDonnees[index].contenu.filter(item => item !== id);
                        }
                        return newDonnees;
                    });
                    MySwal.fire({
                        title: <strong>Zone supprime avec succs</strong>,
                        icon: 'success',
                    });
                } else {
                    MySwal.fire({
                        title: <strong>Erreur lors de la suppression</strong>,
                        icon: 'error',
                    });
                }
            }
            catch (error) {
                MySwal.fire({
                    title: <strong>Erreur lors de la suppression</strong>,
                    icon: 'error',
                });
            }
            finally {
                setVisibility(false);
            }
        }
        else {
            MySwal.fire({
                title: <strong>Suppression annule</strong>,
                icon: 'info',
            });
        }
    }

    const handleAdd = async (values) => {
        if (!values['Ville'] || !values['Dept'] || !values['Zone'] || !values['Km']) {
            MySwal.fire({
                title: <strong>Erreur</strong>,
                html: <i>Tous les champs sont obligatoires.</i>,
                icon: 'error',
            });
            return;
        }
        try {
            const reponse = await fetch(config.URL+'/api/zones/', {
                method: 'POST',
                headers : {
                    'Authorization': `Bearer ${localStorage.getItem('access')}`,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    villes: values['Ville'],
                    dept: values['Dept'],
                    zone: values['Zone'],
                    km: values['Km'],
                }),
            });
            if (reponse.ok) {
                const data = await reponse.json();
                setDonnees((prev) => {
                    const isExisting = prev.some(item => item.contenu.includes(data.id));
                    if (isExisting)
                        return prev;
                    const newDonnees = [...prev];
                    const index = newDonnees.findIndex(item => item.title === 'id');
                    if (index !== -1) {
                        newDonnees[index].contenu.push(data.id);
                    }
                    const index2 = newDonnees.findIndex(item => item.title === 'Ville');
                    if (index2 !== -1) {
                        newDonnees[index2].contenu.push(data.villes);
                    }
                    const index3 = newDonnees.findIndex(item => item.title === 'Dept');
                    if (index3 !== -1) {
                        newDonnees[index3].contenu.push(data.dept);
                    }
                    const index4 = newDonnees.findIndex(item => item.title === 'Zones');
                    if (index4 !== -1) {
                        newDonnees[index4].contenu.push(data.zone);
                    }
                    const index5 = newDonnees.findIndex(item => item.title === 'Kms');
                    if (index5 !== -1) {
                        newDonnees[index5].contenu.push(data.km);
                    }
                    return newDonnees;
                });
                MySwal.fire({
                    title: <strong>Zone ajoute avec succs</strong>,
                    icon: 'success',
                });
            } else {
                MySwal.fire({
                    title: <strong>Erreur lors de l'ajout</strong>,
                    html: '<i>Une erreur inconnue est survenue.</i>',
                    icon: 'error',
                    confirmButtonText: 'OK',
                });
            }
        } catch (error) {
            MySwal.fire({
                title: <strong>Erreur lors de l'ajout</strong>,
                html: '<i>Une erreur inconnue est survenue.</i>',
                icon: 'error',
                confirmButtonText: 'OK',
            });
        }
        finally {
            setVisibility(false);
        }
    }

    return (
        <div style={{ display: 'flex', flexDirection: 'column', gap: '10px',width:'100%',maxHeight:'100vh',overflow:'scroll',justifyContent:'center',alignItems:'center' }}>
            <Detail paramtrage={paramtrage} mode={mode} onSave={handleSave} onDelete={handleDelete} onAdd={handleAdd} onCancel={() => setVisibility(false)} />
        </div>
    );
}

export default DetailZones;