import config from "./config";

const requets = {
    async logIn(username,password) {
        try {
            const reponse = await fetch(config.URL+'/api/token/', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    login: username,
                    password: password,
                }),
            });
            const data = await reponse.json();
            if (reponse.ok) {
                localStorage.setItem('access', data.access);
                localStorage.setItem('refresh', data.refresh);
                const UserInfo = await get(config.URL+'/api/employees/me/');
                if (UserInfo) {
                    return UserInfo.is_superuser;
                }
                return null;
            }
            else {
                return false;
            }
        } catch (error) {
            return null;
        }
    },

    async refreshAccessToken() {
        try {
            let refreshToken = await localStorage.getItem('refresh');
            if (!refreshToken) return null;

            const response = await fetch(config.URL+`/api/token/refresh/`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ refresh: refreshToken }),
            });

            const data = await response.json();
            if (response.ok) {
                localStorage.setItem('access', data.access);
                return data.access;
            }
            else {
                localStorage.removeItem('access');
                localStorage.removeItem('refresh');
                return null;
            }
        }
        catch (error) {
            return null;
        }
    },


    async authFetch(url, options = {}) {
        let accessToken = await localStorage.getItem('access');
        const headers = {
            ...(options.headers || {}),
            Authorization: `Bearer ${accessToken}`,
        };
        if (!(options.body instanceof FormData)) {
            headers['Content-Type'] = 'application/json';
        }

        let res = await fetch(url, {
            ...options,
            headers,
        });


        if (res.status === 401) {
            accessToken = await this.refreshAccessToken();
            if (!accessToken) {
                return null;
            }
            headers.Authorization = `Bearer ${accessToken}`;
            res = await fetch(url, {
                ...options,
                headers,
            });
        }
        return res;
    }
}


export const get = async (url) => {
    try {
        const res = await requets.authFetch(url);
        if (res && !res.ok) {
            return null;
        }
        const data = await res.json();
        return data;
    }catch (error) {
        return null;
    }
}

export default requets;