import React, { useState,useEffect } from 'react';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import interactionPlugin from '@fullcalendar/interaction';
import frLocale from '@fullcalendar/core/locales/fr';

import tippy from 'tippy.js';
import 'tippy.js/dist/tippy.css';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';

import '../styles/Calendrier.css';

import {get} from '../Fonctions/requets';
import config from '../Fonctions/config';

const Calendrier = () => {
  const [events, setEvents] = useState([]);
  const [allEvents, setAllEvents] = useState([]);

  const [currentMonth, setCurrentMonth] = useState(new Date().getMonth() + 1);
  const [currentYear, setCurrentYear] = useState(new Date().getFullYear());

  const [listEmployee, setListEmployee] = useState([]);
  const [selectedEmployee, setSelectedEmployee] = useState(null);
  const [listChantiers, setListChantiers] = useState([]);

  const [listStatus, setListStatus] = useState([]);

  const MySwal = withReactContent(Swal);

  const formatEvent = (eventsList,employeeId) => {
    return eventsList.map(event => {
      return {
        ...event,
        title: employeeId
          ? `${event.chantierName}`
          : `${event.employeeName} ${event.employeeLastName} - ${event.chantierName}`
      };
    });
  };

  useEffect(() => {
    const fetchEmployees = async () => {
      const response = await get(config.URL+'/api/employees/');
      if (!response) {
        console.error('Erreur lors de la rcupration des donnes');
        return;
      }        
      return response;
    };

    const fecthChantiers = async () => {
      const response = await get(config.URL+'/api/chantiers/');
      if (!response) {
        console.error('Erreur lors de la rcupration des donnes');
        return;
      }
      return response;
    };

    const fetchStatus = async () => {
      const response = await get(config.URL+'/api/status_travail/');
      if (!response) {
        console.error('Erreur lors de la rcupration des donnes');
        return;
      }
      response.sort((a, b) => a.id - b.id);
      return response;
    };

    const fetchEvents = async (listEmployee,listChantiers) => {
      const data = await get(config.URL+'/api/presences/');
      if (!data) {
        console.error('Erreur lors de la rcupration des donnes');
        return;
      }
      if (!listEmployee || !listChantiers || !Array.isArray(listEmployee) || !Array.isArray(listChantiers)) {
        console.error('Erreur lors de la rcupration des donnes');
        return;
      }
      const formattedEvents = data.map(presence => {
        const emp = listEmployee.find(emp => emp.id === presence.employee);
        const chantier = listChantiers.find(chantier => chantier.id === presence.chantier);
        return {
          id: presence.id,
          date: presence.date,
          heures: presence.heures,
          employeeId: presence.employee,
          interimaire: presence.interimaire,
          collaborateur: Array.isArray(presence.collaborateurs) && presence.collaborateurs.length > 0
                ? presence.collaborateurs
              .map(id => listEmployee.find(emp => emp.id.toString() === id.toString())?.login)
              .filter(Boolean)
              .join(', ')
                : 'Aucun collaborateur',
          collaborateurs_id: presence.collaborateurs,
          status: presence.status,
          chantierName: chantier ? chantier.name : 'Chantier inconnu',
          employeeName: emp ? emp.name : 'Inconnu',
          employeeLastName: emp ? emp.lastname : 'Inconnu',
        }
      });
      setAllEvents(formattedEvents);
      setEvents(formatEvent(formattedEvents,null));
    };

    const getData = async () =>{
      const employees = await fetchEmployees();
      setListEmployee(employees);
      const chantiers = await fecthChantiers();
      setListChantiers(chantiers);
      fetchEvents(employees,chantiers);
      const status = await fetchStatus();
      setListStatus(status);
    }
    getData();

  }, []);

  const handleDateClick = async (arg) => {
    const {value:formValue} = await MySwal.fire({
      title: <strong>Ajouter une prsence</strong>,
      html: `
      <div>
        <label for="swal-input0"><strong>Employ :</strong></label>
        <select id="swal-input0" class="swal2-select" style="width:16rem; border:1px solid #d9d9d9; border-radius:4px; padding:5px; height:3rem">
          ${listEmployee.map(emp =>
            `<option value="${emp.id}" ${selectedEmployee === emp.id ? 'selected' : ''}>
              ${emp.name} ${emp.lastname}
            </option>`)}
        </select>

        <label for="swal-input"><strong>Chantier :</strong></label>
        <select id="swal-input" class="swal2-select" style="width:16rem; border:1px solid #d9d9d9; border-radius:4px; padding:5px; height:3rem">
          ${listChantiers.map(chantier =>
            `<option value="${chantier.id}">
              ${chantier.name}
            </option>`)}
        </select>

        <label for="swal-input1"><strong>Heures :</strong></label>
        <input id="swal-input1" class="swal2-input" type="number" value="0" min="0" max="24" placeholder="Heures" style="width:16rem">

        <label for="swal-input2"><strong>Status :</strong></label>
        <select id="swal-input2" class="swal2-select" style="width:16rem; border:1px solid #d9d9d9; border-radius:4px; padding:5px; height:3rem">
          ${listStatus.map(status =>
            `<option value="${status.name}">
              ${status.name}
            </option>`)}
        </select>

        <label for="swal-input3"><strong>Interimaires :</strong></label>
        <input id="swal-input3" class="swal2-input" placeholder="Intrimaires" value="0" type="number" min="0" max="4" style="width:16rem">
        
        <label for="swal-input4"><strong>Date :</strong></label>
        <input id="swal-input4" type="date" class="swal2-input" value="${arg.dateStr}" style="width:16rem">
      </div>
      `,
      icon: 'info',
      confirmButtonText: 'Suivant',
      cancelButtonText: 'Annuler',
      cancelButtonColor: 'red',
      showCancelButton: true,
      confirmButtonColor: '#52bdf1',
      allowOutsideClick: () => !Swal.isLoading(),
      'preConfirm': () => {
        return {
          'chantier': document.getElementById('swal-input').value,
          'heures': document.getElementById('swal-input1').value,
          'status': document.getElementById('swal-input2').value,
          'interimaire': document.getElementById('swal-input3').value,
          'date': document.getElementById('swal-input4').value,
          'employee': document.getElementById('swal-input0').value,
        }
      }})
    if (formValue){
      const {value: collaborateurs} = await MySwal.fire({
        title: <strong>Liste Collaborateurs</strong>,
        html: `
          <div style="max-height: 200px; overflow-y: auto; padding: 5px 10px; border: 1px solid #ccc; border-radius: 4px;">
            ${listEmployee.map(emp => `
              <div style="display: flex; align-items: center; gap: 10px; margin-bottom: 5px;">
              <input type="checkbox" id="cb-${emp.id}" name="collaborateurs" value="${emp.id}">
              <label for="cb-${emp.id}">${emp.name} ${emp.lastname}</label>
            </div>
          `).join('')}
          </div>
        `,
        icon: 'info',
        showCancelButton: true,
        confirmButtonText: 'Valider',
        cancelButtonText: 'Annuler',
        cancelButtonColor: 'red',
        confirmButtonColor: '#52bdf1',
        preConfirm: () => {
          const checkboxes = document.querySelectorAll('input[name="collaborateurs"]:checked');
          const selectedCollaborateurs = Array.from(checkboxes).map(cb => cb.value);
          return selectedCollaborateurs;
        }
      });
      if (collaborateurs) {
        const reponse = await fetch(config.URL+'/api/presences/', {
          method: 'POST',
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('access')}`,
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            'heures': formValue.heures,
            'status': formValue.status,
            'interimaire': formValue.interimaire,
            'date': formValue.date,
            'employee': formValue.employee,
            'chantier': formValue.chantier,
            'collaborateurs': collaborateurs
          }),
        });
        if (reponse.ok) {
          MySwal.fire({
            title: <strong>vnement ajout avec succs</strong>,
            icon: 'success',
          });
          const data = await reponse.json();
          const newEvent = {
            id: data.id,
            date: data.date,
            heures: data.heures,
            employeeId: data.employee,
            interimaire: data.interimaire,
            collaborateur : Array.isArray(data.collaborateurs) && data.collaborateurs.length > 0
              ? data.collaborateurs
                .map(id => listEmployee.find(emp => emp.id.toString() === id.toString())?.login)
                .filter(Boolean)
                .join(', ')
              : 'Aucun collaborateur',
            collaborateurs_id: data.collaborateurs,
            status: data.status,
            chantierName: listChantiers.find(chantier => chantier.id === parseInt(data.chantier)).name,
            employeeName: listEmployee.find(emp => emp.id === parseInt(data.employee)).name,
            employeeLastName: listEmployee.find(emp => emp.id === parseInt(data.employee)).lastname,
          };
          setAllEvents([...allEvents, newEvent]);
          setEvents(formatEvent([...allEvents, newEvent],selectedEmployee));
        }
        else {
          MySwal.fire({
            title: <strong>Erreur lors de l'ajout</strong>,
            icon: 'error',
          });
        }
      }
    }
  };

  const handleEventClick =async (arg) => {
    const eventId = arg.event.id;
    const rep = await MySwal.fire({
      title:"Details",
      html: `
        <div>
          <p><strong>Prnom/Nom :</strong> ${arg.event.extendedProps.employeeName} ${arg.event.extendedProps.employeeLastName}</p>
          <p><strong>Chantier :</strong> ${arg.event.extendedProps.chantierName}</p>
          <p><strong>Nombre d'interimaires :</strong> ${arg.event.extendedProps.interimaire}</p>
          <p><strong>Date :</strong> ${arg.event.start.toLocaleDateString()}</p>
          <p><strong>Heures :</strong> ${arg.event.extendedProps.heures}</p>
          <p><strong>Status :</strong> ${arg.event.extendedProps.status}</p>
          <p><strong>Collaborateurs :</strong> ${arg.event.extendedProps.collaborateur}</p>
        </div>
      `,
      confirmButtonText: 'Ok',
      confirmButtonColor: '#52bdf1',
      showCancelButton: true,
      showDenyButton: true,
      denyButtonText: 'Modifier',
      denyButtonColor: '#52bdf1',
      cancelButtonText: 'Supprimer',
      cancelButtonColor: 'red',
      allowOutsideClick: () => !Swal.isLoading()
    });
    if (rep.isDismissed && rep.dismiss === Swal.DismissReason.cancel) {
      MySwal.fire({
          title: <strong>tes-vous sr de vouloir supprimer cet vnement ?</strong>,
          html: <i>Cette action est irrversible.</i>,
          icon: 'warning',
          showCancelButton: true,
          confirmButtonText: 'Oui',
          cancelButtonText: 'Non',
        }).then(async (result) => {
          if (result.isConfirmed) {
            const response = await fetch(config.URL+`/api/presences/${eventId}/`, {
              method: 'DELETE',
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('access')}`,
                'Content-Type': 'application/json',
              },
            });
            if (response.ok) {
              MySwal.fire({
                title: <strong>vnement supprim avec succs</strong>,
                icon: 'success',
              });
              setEvents(events.filter(event => event.id.toString() !== eventId.toString()));
              setAllEvents(allEvents.filter(event => event.id.toString() !== eventId.toString()));
            } else {
              MySwal.fire({
                title: <strong>Erreur lors de la suppression</strong>,
                icon: 'error',
              });
            }
          }
        });  
    }
    else if(rep.isDenied){
      const {value: formValues } = await MySwal.fire({
        'title': `Modifier la prsence de ${arg.event.extendedProps.employeeName} ${arg.event.extendedProps.employeeLastName}`,
        'html': `
          <div style="display: grid; grid-template-columns: 150px 1fr; row-gap: 10px; column-gap: 10px; align-items: center;">
    
    <label for="swal-input"><strong>Chantier :</strong></label>
    <select id="swal-input" class="swal2-select" style="width:16rem; border:1px solid #d9d9d9; border-radius:4px; padding:5px; height:3rem">
      ${listChantiers.map(chantier =>
        `<option value="${chantier.id}" ${arg.event.extendedProps.chantierName === chantier.name ? 'selected' : ''}>
          ${chantier.name}
        </option>`)}
    </select>

    <label for="swal-input3"><strong>Interimaires :</strong></label>
    <input id="swal-input3" class="swal2-input" placeholder="Intrimaires" value="${arg.event.extendedProps.interimaire}" style="width:16rem">

    <label for="swal-input0"><strong>Date :</strong></label>
    <input id="swal-input0" type="date" class="swal2-input" value="${arg.event.start.toLocaleDateString('en-CA')}" style="width:16rem">

    <label for="swal-input1"><strong>Heures :</strong></label>
    <input id="swal-input1" class="swal2-input" placeholder="Heures" value="${arg.event.extendedProps.heures}" style="width:16rem">

    <label for="swal-input2"><strong>Status :</strong></label>
    <select id="swal-input2" class="swal2-select" style="width:16rem; border:1px solid #d9d9d9; border-radius:4px; padding:5px; height:3rem">
    ${listStatus.map(status =>
      `<option value="${status.name}" ${arg.event.extendedProps.status === status.name ? 'selected' : ''}>
        ${status.name}
      </option>`)}
      
    </select>
  
  </div>
        `,
        'focusConfirm': false,
        'showCancelButton': true,
        'confirmButtonText': 'Suivant',
        'cancelButtonText': 'Annuler',
        'cancelButtonColor': 'red',
        'confirmButtonColor': '#52bdf1',
        'preConfirm': () => {
          return {
            'heures': document.getElementById('swal-input1').value,
            'status': document.getElementById('swal-input2').value,
            'interimaire': document.getElementById('swal-input3').value,
            'date': document.getElementById('swal-input0').value,
            'chantier': document.getElementById('swal-input').value,
          }
        }
      });
    if (formValues) {
      const {value: collaborateurs} = await MySwal.fire({
        title: <strong>Liste Collaborateurs</strong>,
        html: `
          <div style="max-height: 200px; overflow-y: auto; padding: 5px 10px; border: 1px solid #ccc; border-radius: 4px;">
            ${listEmployee.map(emp => `
              <div style="display: flex; align-items: center; gap: 10px; margin-bottom: 5px;">
              <input type="checkbox" id="cb-${emp.id}" name="collaborateurs" value="${emp.id}"
                ${arg.event.extendedProps.collaborateurs_id.includes(emp.id) ? 'checked' : ''}>
              <label for="cb-${emp.id}">${emp.name} ${emp.lastname}</label>
            </div>
          `).join('')}
          </div>
        `,
        icon: 'info',
        showCancelButton: true,
        confirmButtonText: 'Valider',
        cancelButtonText: 'Annuler',
        cancelButtonColor: 'red',
        confirmButtonColor: '#52bdf1',
        preConfirm: () => {
          const checkboxes = document.querySelectorAll('input[name="collaborateurs"]:checked');
          const selectedCollaborateurs = Array.from(checkboxes).map(cb => cb.value);
          return selectedCollaborateurs;
        }
      });
      if (collaborateurs) {
        const fetchrep = await fetch(config.URL+`/api/presences/${eventId}/`, {
          method: 'PATCH',
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('access')}`,
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            'heures': formValues.heures,
            'status': formValues.status,
            'interimaire': formValues.interimaire,
            'date': formValues.date,
            'chantier': formValues.chantier,
            'collaborateurs': collaborateurs
          }),
        })
        if (fetchrep.ok) {
          MySwal.fire({
            title: <strong>vnement modifi avec succs</strong>,
            icon: 'success',
          });
          const data = await fetchrep.json();
          const updatedEvents = allEvents.map(event => {
            if (event.id.toString() === eventId.toString()) {
              return {
                ...event,
                id: data.id,
                date: data.date,
                heures: data.heures,
                employeeId: data.employee,
                interimaire: data.interimaire,
                collaborateur : Array.isArray(data.collaborateurs) && data.collaborateurs.length > 0
                ? data.collaborateurs
                  .map(id => listEmployee.find(emp => emp.id.toString() === id.toString())?.login)
                  .filter(Boolean)
                  .join(', ')
                : 'Aucun collaborateur',
                collaborateurs_id: data.collaborateurs,
                status: data.status,
                chantierName: listChantiers.find(chantier => chantier.id === parseInt(data.chantier)).name,
                employeeName: event.employeeName,
                employeeLastName: event.employeeLastName,
              };
            }
            return event;
          });
          setAllEvents(updatedEvents);
          setEvents(formatEvent(updatedEvents,selectedEmployee));
        } else {
          MySwal.fire({
            title: <strong>Erreur lors de la modification</strong>,
            icon: 'error',
          });
        }
      }
    }
    }
  }

  const exportCalendar = async () =>{
    const rep = await fetch(config.URL+'/api/export/excel/?month=' + currentMonth + '&year=' + currentYear);
    const blob = await rep.blob();
    const disposition = rep.headers.get("Content-Disposition");
    let fileName = "calendrier.xlsx";

    if (disposition && disposition.includes("filename=")) {
        fileName = disposition.split("filename=")[1].replace(/['"]/g, "");
    }
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = fileName;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    window.URL.revokeObjectURL(url);
  }

  const handleEmployeeClick = (employeeId) => {
    if (selectedEmployee === employeeId) {
      setSelectedEmployee(null);
      setEvents(formatEvent(allEvents,null));
    } else {
      setSelectedEmployee(employeeId);
      const filteredEvents = allEvents.filter(event => event.employeeId === employeeId);
      setEvents(formatEvent(filteredEvents,employeeId));
    }
  }

  return (
    <div style={{ padding: '20px' }}>
      <h1 style={{fontSize: '40px', fontWeight: 'bold', textAlign: 'center', marginBottom: '20px'}}>
        Calendrier
      </h1>
      <button style={{
          marginBottom: '10px',
          backgroundColor: '#52bdf1',
          color: '#fff',
          border: 'none',
          borderRadius: '5px',
          padding: '10px 20px',
          fontSize: '16px',
          fontWeight: 'bold',
          cursor: 'pointer',
         }} 
         onClick={() => exportCalendar()}>
        Exporter
      </button>
      <FullCalendar
          plugins={[dayGridPlugin, interactionPlugin]}
          initialView="dayGridMonth"
          locale={frLocale}
          events={events}
          dateClick={handleDateClick}
          eventClick={handleEventClick}
          eventMouseEnter={(info) => {
            tippy(info.el, {
              content: `Voir les dtails`,
              theme: 'light',
              arrow: true,
              delay: 0,
            });
            const el = info.el;
            el.style.cursor = 'pointer';
          
          }}
          height="auto"
          datesSet={(arg) => {
            const currentDate = arg.view.currentStart;
            const month = currentDate.getMonth() + 1;
            const year = currentDate.getFullYear();
            setCurrentMonth(month);
            setCurrentYear(year);
          }}
      />  
      <div style={{ marginTop: '20px', display: 'flex', flexDirection: 'row', alignItems: 'center', gap: '10px',flexWrap: 'wrap',width: '100%',justifyContent: 'center' }}>
        {listEmployee && listEmployee.map((emp) => (
          <button 
            key={emp.id} 
            style={{ 
              margin: '10px 0', 
              fontSize: '16px', 
              backgroundColor: selectedEmployee === emp.id ? '#52bdf1' : '#ccc',
              color: '#fff', 
              padding: '10px 20px', 
              borderRadius: '5px', 
              border: 'none', 
              cursor: 'pointer' 
            }}
            onClick={() => handleEmployeeClick(emp.id)}
          >
            {emp.name && emp.lastname ? `${emp.name} ${emp.lastname}` : emp.login}
          </button>
        ))}
      </div>
    </div>
  );
};

export default Calendrier;
