import {useState,useEffect} from 'react';
import styles from '../../styles/Detail.module.css';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';
import { FaTrash } from "react-icons/fa";


import JSZip from 'jszip';
import { saveAs } from 'file-saver';

import Detail from '../Detail';
import PhotoWithButton from '../Utils/PhotoWithButton';

import {get} from '../../Fonctions/requets';
import config from '../../Fonctions/config';

const DetailChantiers = ({id,setVisibility,mode,setDonnees}) => {
    const [infos, setInfos] = useState({});
    const MySwal = withReactContent(Swal);
    useEffect(() => {
        const fetchData = async () => {
            const data = await get(config.URL+`/api/chantiers/${id}/`);
            if (!data) {
                setInfos({});
                return;
            }
            setInfos(data);
        }
        if (mode === "Ajout")
            return;
        fetchData();
    }, [id,mode]);

    const [photos, setPhotos] = useState([]);
    useEffect(() => {
        const fetchPhotos = async () => {
            const data = await get(config.URL+`/api/chantiers/${id}/photos/`);
            if (!data) {
                setPhotos([]);
                return;
            }
            setPhotos(data);
        }
        if (mode === "Ajout")
            return;
        fetchPhotos();
    }, [id,mode]);

    const [documents, setDocuments] = useState([]);
    useEffect(() => {
        const fetchDocuments = async () => {
            const data = await get(config.URL+`/api/chantiers/${id}/documents/`);
            if (!data) {
                setDocuments([]);
                return;
            }
            setDocuments(data);
        }
        if (mode === "Ajout")
            return;
        fetchDocuments();
    }, [id,mode]);

    const [zones, setZones] = useState([]);
    useEffect(() => {
        const fetchZones = async () => {
            const data = await get(config.URL+`/api/zones/`);
            if (!data) {
                setZones([]);
                return;
            }
            data.map((zone) => {
                zone.label = zone.villes;
                zone.value = zone.id;
                return zone;
            })
            setZones(data);
            setParamtrage((prev) => {
                const updatedParam = prev.map((param) => {
                    if (param.label === 'Zone') {
                        return { ...param, value: data };
                    }
                    return param;
                });
                return updatedParam;
            }
            );
        }
        fetchZones();
    }, []);

    const [paramtrage, setParamtrage] = useState([]);
    useEffect(() => {
        setParamtrage([
            { label: 'Nom', value: infos.name, type: 'input' },
            { label: 'Zone', value: zones, type: 'select', idx: zones.findIndex(zone => zone.id === infos.zone) !== -1 ? zones.findIndex(zone => zone.id === infos.zone) : 0 
            },
        ]);
    }, [infos, zones]);


    const handleSave = async (values) => {
        if (!values['Nom'] || values['Nom'] === '') {
            MySwal.fire({
                title: <strong>Veuillez remplir tous les champs</strong>,
                icon: 'error',
            });
            return;
        }
        const reponse = await fetch(config.URL+`/api/chantiers/${id}/`, {
            method: 'PATCH',
            headers: {
                'Authorization': `Bearer ${localStorage.getItem('access')}`,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                name: values['Nom'],
                zone: values['Zone'],
            }),
        });
        if (reponse.ok) {
            const data = await reponse.json();
            setDonnees((prev) => {
                const newDonnees = [...prev];
                const index = newDonnees.findIndex(item => item.title === 'id');
                let idx = -1;
                if (index !== -1) {
                    idx = newDonnees[index].contenu.findIndex(item => item === id);
                    newDonnees[index].contenu = newDonnees[index].contenu.map(item => item === id ? data.id : item);
                }
                const index2 = newDonnees.findIndex(item => item.title === 'Nom');
                if (index2 !== -1) {
                    newDonnees[index2].contenu = newDonnees[index2].contenu.map(item => item === infos.name ? data.name : item);
                } 
                const index3 = newDonnees.findIndex(item => item.title === 'Ville');
                if (index3 !== -1 && idx !== -1) {
                    newDonnees[index3].contenu[idx] = zones.find(zone => zone.id === data.zone).villes;
                }
                return newDonnees;
            });
            MySwal.fire({
                title: <strong>Chantier modifi avec succs</strong>,
                icon: 'success',
            });
            setVisibility(false);
        } else {
            MySwal.fire({
                title: <strong>Erreur lors de la modification</strong>,
                icon: 'error',
            });
        }
    }

    const handleDelete = async () => {
        const result = await MySwal.fire({
            title: <strong>tes-vous sr de vouloir supprimer ce chantier ?</strong>,
            html : <i>Cette action supprimera toutes les donnes lies (photos, documents, etc...)</i>,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Oui',
            cancelButtonText: 'Non',
        });
        if (result.isConfirmed) {
            const response = await fetch(config.URL+`/api/chantiers/${id}/`, {
                method: 'DELETE',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('access')}`,
                    'Content-Type': 'application/json',
                },
            });
            if (response.ok) {
                setDonnees((prev) => {
                    const newDonnees = [...prev];
                    const index = newDonnees.findIndex(item => item.title === 'id');
                    if (index !== -1) {
                        newDonnees[index].contenu = newDonnees[index].contenu.filter(item => item !== id);
                    }
                    return newDonnees;
                });
                    
                MySwal.fire({
                    title: <strong>Chantier supprim avec succs</strong>,
                    icon: 'success',
                });
                setVisibility(false);
            } else {
                MySwal.fire({
                    title: <strong>Erreur lors de la suppression</strong>,
                    icon: 'error',
                });
            }
        }
        else{
            MySwal.fire({
                title: <strong>Suppression annule</strong>,
                icon: 'info',
            });
        }
    }

    const handleAdd = async (values) => {
        if (!values['Nom'] || values['Nom'] === '') {
            MySwal.fire({
                title: <strong>Veuillez remplir tous les champs</strong>,
                icon: 'error',
            });
            return;
        }
        const reponse = await fetch(config.URL+'/api/chantiers/', {
            method: 'POST',
            headers: {
                'Authorization': `Bearer ${localStorage.getItem('access')}`,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                name: values['Nom'],
                zone: values['Zone'],
            }),
        });
        if (reponse.ok) {
            const data = await reponse.json();
            setDonnees((prev) => {
                const isExisting = prev.some(item => item.title === 'id' && item.contenu.includes(data.id));
                if (isExisting) {
                    return prev;
                }
                const newDonnees = [...prev];
                const index = newDonnees.findIndex(item => item.title === 'id');
                if (index !== -1) {
                    newDonnees[index].contenu = [...newDonnees[index].contenu, data.id];
                }
                const index2 = newDonnees.findIndex(item => item.title === 'Nom');
                if (index2 !== -1) {
                    newDonnees[index2].contenu = [...newDonnees[index2].contenu, data.name];
                }
                const index3 = newDonnees.findIndex(item => item.title === 'Ville');
                if (index3 !== -1) {
                    newDonnees[index3].contenu = [...newDonnees[index3].contenu, zones.find(zone => zone.id === data.zone).villes];
                }
                return newDonnees;
            });

            MySwal.fire({
                title: <strong>Chantier ajout avec succs</strong>,
                icon: 'success',
            });
            setVisibility(false);
        } else {
            MySwal.fire({
                title: <strong>Erreur lors de l'ajout</strong>,
                icon: 'error',
            });
        }
    }

    const deletePhoto = async (id) => {
        const result = await MySwal.fire({
            title: <strong>tes-vous sr de vouloir supprimer cette photo ?</strong>,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Oui',
            cancelButtonText: 'Non',
        });
        if (result.isConfirmed) {
            const response = await fetch(config.URL+`/api/photos_chantier/${id}/`, {
                method: 'DELETE',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('access')}`,
                    'Content-Type': 'application/json',
                },
            });
            if (response.ok) {
                MySwal.fire({
                    title: <strong>Photo supprime avec succs</strong>,
                    icon: 'success',
                });
                setPhotos(photos.filter((photo) => photo.id !== id));
            } else {
                MySwal.fire({
                    title: <strong>Erreur lors de la suppression</strong>,
                    icon: 'error',
                });
            }
        }
    }

    const SetDownloaded = async (id) => {
        const response = await fetch(config.URL+`/api/photos_chantier/${id}/`, {
            method: 'PATCH',
            headers: {
                'Authorization': `Bearer ${localStorage.getItem('access')}`,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                has_been_downloaded: true,
            }),
        });
        if (response.ok) {
            setPhotos(photos.map((p) => p.id === id ? { ...p, has_been_downloaded: true } : p));
        }
    }

    const ExportPhotos = async () => {
        const zip = new JSZip();
        for (let i = 0; i < photos.length; i++) {
            const photo = photos[i];
            const response = await fetch(photo.photo);
            const blob = await response.blob();
            const extension = photo.name?.split('.').pop() || 'jpg';
            zip.file(`photo_${i + 1}.${extension}`, blob);
            await SetDownloaded(photo.id);
            photo.has_been_downloaded = true;
        }
        const zipBlob = await zip.generateAsync({ type: 'blob' });
        saveAs(zipBlob, `photos_${infos.name}.zip`);
    }

    const AddPhoto = async () => {
        const input = document.createElement('input');
        input.type = 'file';
        input.accept = 'image/*';
        input.onchange = async (event) => {
            const file = event.target.files[0];
            if (file) {
                const formData = new FormData();
                formData.append('photo', file);
                formData.append('chantier', id);
                formData.append('date', new Date().toISOString());

                const response = await fetch(config.URL+`/api/photos_chantier/`, {
                    method: 'POST',
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('access')}`,
                    },
                    body: formData,
                });
                const data = await response.json();
                
                if (response.ok) {
                    MySwal.fire({
                        title: <strong>Photo ajoute avec succs</strong>,
                        icon: 'success',
                    });
                    setPhotos([...photos, ...data]);
                } else {
                    MySwal.fire({
                        title: <strong>Erreur lors de l'ajout de la photo</strong>,
                        icon: 'error',
                    });
                }
            }
        };
        input.click();
    }

    const AddDocument = async () => {
        const input = document.createElement('input');
        input.type = 'file';
        input.accept = '*/*';
        input.onchange = async (event) => {
            const file = event.target.files[0];
            if (file) {
                const formData = new FormData();
                formData.append('document', file);
                formData.append('chantier', id);
                formData.append('name', file.name);

                const response = await fetch(config.URL+`/api/documents_chantier/`, {
                    method: 'POST',
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('access')}`,
                    },
                    body: formData,
                });
                const data = await response.json();
                
                if (response.ok) {
                    MySwal.fire({
                        title: <strong>Document ajout avec succs</strong>,
                        icon: 'success',
                    });
                    setDocuments([...documents, data]);
                } else {
                    MySwal.fire({
                        title: <strong>Erreur lors de l'ajout du document</strong>,
                        icon: 'error',
                    });
                }
            }
        };
        input.click();
    }

    const deleteDocument = async (id) => {
        const result = await MySwal.fire({
            title: <strong>tes-vous sr de vouloir supprimer ce document ?</strong>,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Oui',
            cancelButtonText: 'Non',
        });
        if (result.isConfirmed) {
            const response = await fetch(config.URL+`/api/documents_chantier/${id}/`, {
                method: 'DELETE',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('access')}`,
                    'Content-Type': 'application/json',
                },
            });
            if (response.ok) {
                MySwal.fire({
                    title: <strong>Document supprim avec succs</strong>,
                    icon: 'success',
                });
                setDocuments(documents.filter((document) => document.id !== id));
            } else {
                MySwal.fire({
                    title: <strong>Erreur lors de la suppression</strong>,
                    icon: 'error',
                });
            }
        }
    }

    const clearPhotos = async () => {
        const result = await MySwal.fire({
            title: <strong>tes-vous sr de vouloir supprimer toutes les photos ?</strong>,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Oui',
            cancelButtonText: 'Non',
        });
        if (result.isConfirmed) {
            for (let i = 0; i < photos.length; i++) {
                const photo = photos[i];
                const response = await fetch(config.URL+`/api/photos_chantier/${photo.id}/`, {
                    method: 'DELETE',
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('access')}`,
                        'Content-Type': 'application/json',
                    },
                });
                if (response.ok) {
                    setPhotos(photos.filter((p) => p.id !== photo.id));
                } else {
                    MySwal.fire({
                        title: <strong>Erreur lors de la suppression</strong>,
                        icon: 'error',
                    });
                    return;
                }
            }
            MySwal.fire({
                title: <strong>Toutes les photos ont t supprimes avec succs</strong>,
                icon: 'success',
            });
        }
        else {
            MySwal.fire({
                title: <strong>Suppression annule</strong>,
                icon: 'info',
            });
        }
    }

    if (mode === "Ajout") {
        return (
            <div style={{ display: 'flex', flexDirection: 'column', gap: '10px',width:'100%',maxHeight:'100vh',overflow:'scroll',justifyContent:'center',alignItems:'center' }}>
                <Detail paramtrage={paramtrage} mode={mode} onSave={handleSave} onDelete={handleDelete} onAdd={handleAdd} onCancel={() => setVisibility(false)} />
            </div>
        )
    }

    return (
        <div style={{ display: 'flex', flexDirection: 'column', gap: '10px', width: '100%', maxHeight: '100vh', overflow: 'scroll', justifyContent: 'center', alignItems: 'center' }}>
            <Detail paramtrage={paramtrage} mode={mode} onSave={handleSave} onDelete={handleDelete} onAdd={handleAdd} onCancel={() => setVisibility(false)} />
            <div className={styles.card} style={{ maxWidth: '90%', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <div style={{ display: 'flex', flexDirection: 'row', gap: '10px', justifyContent: 'center', alignItems: 'center' }}>
                    <div className={styles.title}>
                        Photos
                    </div>
                    <button style={{ background: 'none', border: 'none', cursor: 'pointer' }} onClick={clearPhotos}>
                        <FaTrash style={{ color: 'red', fontSize: '20px' }} />
                    </button>
                </div>
                <button className={styles.button} onClick={AddPhoto}>
                    Ajouter
                </button>
                {photos.length > 0 &&
                    <button className={styles.button} onClick={ExportPhotos}>
                        Exporter
                    </button>
                }
                <div style={{ display: 'flex', flexDirection: 'row', gap: '10px', justifyContent: 'center', alignItems: 'center', flexWrap: 'wrap' }}>
                    {photos.length === 0 && <p style={{ textAlign: 'center', margin: '10px 0' }}>Aucune photo n'a encore t ajoute</p>}
                    {photos.map((photo, index) => (
                        <PhotoWithButton
                            key={index}
                            photo={`${photo.photo}`}
                            alt={`${infos.name}_${photo.date}`}
                            style={{ width: '100px', height: '100px',filter: photo.has_been_downloaded ? 'grayscale(100%)' : 'none' }}
                            onDelete={async () => await deletePhoto(photo.id)}
                            onDownload={async () => await SetDownloaded(photo.id)}
                        />
                    ))}
                </div>
            </div>
            <div className={styles.card} style={{ maxWidth: '50%', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                <div className={styles.title}>
                    Documents
                </div>
                <button className={styles.button} onClick={AddDocument}>
                    Ajouter
                </button>
                {documents.length === 0 && <p style={{ textAlign: 'center', margin: '10px 0' }}>Aucun document n'a encore t ajout</p>}
                {documents.map((document, index) => (
                    <div key={index} style={{ display: 'flex', flexDirection: 'row', gap: '10px', justifyContent: 'center', alignItems: 'center' }}>
                        <a href={`${document.document}`} target="_blank" rel="noopener noreferrer" style={{ textDecoration: 'none', color: '#007BFF' }}>
                            {document.name}
                        </a>
                        <button
                            style={{
                                padding: '5px 10px',
                                backgroundColor: '#ff4d4d',
                                color: 'white',
                                border: 'none',
                                borderRadius: '5px',
                                cursor: 'pointer',
                                transition: 'background-color 0.3s',
                            }}
                            onClick={() => deleteDocument(document.id)}
                        >
                            <FaTrash />
                        </button>
                    </div>
                ))}
            </div>
        </div>
    )
}

export default DetailChantiers;