import React from 'react';
import styles from '../styles/SideBandeau.module.css';

import { get } from '../Fonctions/requets';
import config from '../Fonctions/config';
import {FaCalendar,
        FaMapMarkerAlt,
        FaUsers,
        FaBuilding,
        FaClipboardList,
        FaCalendarCheck
    } 
    from "react-icons/fa";


const SideBandeau = ({onSelect,selected,setDonnees,setDetail}) => {

    const getZone = async () => {        
        const data = await get(config.URL+'/api/zones/');
        if (!data) {
            console.error('Erreur lors de la rcupration des donnes');
            return;
        }
        const sortedData = data.sort((a, b) => a.id - b.id);
        const donnees = [
            {
                title: 'id',
                contenu: sortedData.map(item => item.id),
            },
            {
                title: 'Ville',
                contenu: sortedData.map(item => item.villes),
            },
            {
                title: 'Dept',
                contenu: sortedData.map(item => item.dept),
            },
            {
                title: 'Zones',
                contenu: sortedData.map(item => item.zone),
            },
            {
                title: 'Kms',
                contenu: sortedData.map(item => item.km),
            }
        ]
        setDonnees(donnees);
        onSelect('Zones');
        setDetail(false);
    }

    const getEmploye = async () => {
        const data = await get(config.URL+'/api/employees/');
        if (!data) {
            console.error('Erreur lors de la rcupration des donnes');
            return;
        }
        const sortedData = data.sort((a, b) => a.id - b.id);
        setDonnees([
            {
                title: 'id',
                contenu: sortedData.map(item => item.id),
            },
            {
                title: 'Identifiant',
                contenu: sortedData.map(item => item.login),
            },
            {
                title: 'Prnom',
                contenu: sortedData.map(item => item.name),
            },
            {
                title: 'Nom',
                contenu: sortedData.map(item => item.lastname),
            },
            {
                title: 'Contrat',
                contenu: sortedData.map(item => item.contrat),
            },
            {
                title : 'Numro de tlphone',
                contenu: sortedData.map(item => item.telephone),
            },
        ]);
        onSelect('Employs');
        setDetail(false);
    }

    const getChantier = async () => {
        const data = await get(config.URL+'/api/chantiers/');
        if (!data) {
            console.error('Erreur lors de la rcupration des donnes');
            return;
        }
        const zones = await get(config.URL+'/api/zones/');
        if (!zones) {
            console.error('Erreur lors de la rcupration des zones');
            return;
        }
        const sortedData = data.sort((a, b) => a.id - b.id);
        setDonnees([{
            title: 'id',
            contenu: sortedData.map(item => item.id),
        },
        {
            title: 'Nom',
            contenu: sortedData.map(item => item.name),
        },
        {
            title: 'Ville',
            contenu: sortedData.map(item => {
                const zone = zones.find(zone => zone.id === item.zone);
                return zone ? zone.villes : '';
            }
            ),
        },
        ]);
        onSelect('Chantiers');
        setDetail(false);
    }

    const getFormation = async () => {
        const data = await get(config.URL+'/api/formations/');
        if (!data) {
            console.error('Erreur lors de la rcupration des donnes');
            return;
        }
        const sortedData = data.sort((a, b) => a.id - b.id);
        setDonnees([{
            title: 'id',
            contenu: sortedData.map(item => item.id),
        },
        {
            title: 'Nom',
            contenu: sortedData.map(item => item.name),
        },
        ]);
        onSelect('Formations');
        setDetail(false);
    }

    const getStatusTravail = async () => {
        const data = await get(config.URL+'/api/status_travail/');
        if (!data) {
            console.error('Erreur lors de la rcupration des donnes');
            return;
        }
        const sortedData = data.sort((a, b) => a.id - b.id);
        setDonnees([{
            title: 'id',
            contenu: sortedData.map(item => item.id),
        },
        {
            title: 'Nom',
            contenu: sortedData.map(item => item.name),
        },
        {
            title: 'Raccourcis',
            contenu: sortedData.map(item => item.raccourcis),
        }
        ]);
        onSelect('StatusTravail');
        setDetail(false);
    }

    return (
        <div className={styles.container}>
            <button 
                className={styles.onglet} 
                onClick={() => onSelect('Calendrier')}
                style={{ color: selected === 'Calendrier' ? '#52bdf1' : '#333', borderBottom: selected === 'Calendrier' ? '2px solid #52bdf1' : '1px solid #ccc' }}
            >
                <FaCalendar/> <span>Calendrier</span>
            </button>
            <button 
                className={styles.onglet} 
                onClick={() =>{getZone()}}
                style={{ color: selected === 'Zones' ? '#52bdf1' : '#333', borderBottom: selected === 'Zones' ? '2px solid #52bdf1' : '1px solid #ccc' }}
            >
                <FaMapMarkerAlt /> <span>Zones</span>
            </button>
            <button 
                className={styles.onglet} 
                onClick={() => {getEmploye()}}
                style={{ color: selected === 'Employs' ? '#52bdf1' : '#333', borderBottom: selected === 'Employs' ? '2px solid #52bdf1' : '1px solid #ccc' }}
            >
                <FaUsers/> <span>Employs</span>
            </button>
            <button 
                className={styles.onglet} 
                onClick={() => {getChantier()}}
                style={{ color: selected === 'Chantiers' ? '#52bdf1' : '#333', borderBottom: selected === 'Chantiers' ? '2px solid #52bdf1' : '1px solid #ccc' }}
            >
                <FaBuilding /> <span>Chantiers</span>
            </button>
            <button 
                className={styles.onglet} 
                onClick={() => {getFormation()}}
                style={{ color: selected === 'Formations' ? '#52bdf1' : '#333', borderBottom: selected === 'Formations' ? '2px solid #52bdf1' : '1px solid #ccc' }}
            >
                <FaClipboardList /> <span>Formations</span>
            </button>
            <button 
                className={styles.onglet} 
                onClick={() => {getStatusTravail()}}
                style={{ color: selected === 'StatusTravail' ? '#52bdf1' : '#333', borderBottom: selected === 'StatusTravail' ? '2px solid #52bdf1' : '1px solid #ccc' }}
            >
                <FaCalendarCheck /> <span>Status de travail</span>
            </button>
        </div>
    );
};

export default SideBandeau;