import './App.css';
import React, { useState } from 'react';
import Tableau from './Components/Tableau';
import SideBandeau from './Components/SideBandeau';
import Calendrier from './Components/Calendrier';


import DetailChantiers from './Components/Detail/Chantiers';
import DetailZones from './Components/Detail/Zones';
import DetailEmployee from './Components/Detail/Employees';
import DetailFormations from './Components/Detail/Formations';
import DetailStatusTravail from './Components/Detail/StatusTravail';

import Login from './Components/Login';

function App() {
  const [selectedTab, setSelectedTab] = useState('Calendrier');
  const [donnees, setdonnees] = useState([]);
  const [detail, setDetail] = useState(false);

  const [isLoggedIn, setIsLoggedIn] = useState(false);


  const renderRightComponent = () => {
    switch (selectedTab) {
      case 'Calendrier':
        return <Calendrier />;
      case 'Zones':
        return <Tableau 
                title="Zones" 
                donnees={donnees} 
                setDonnees={setdonnees}
                ComponentDetail={DetailZones} 
                detail={detail} 
                setDetailVisible={setDetail}
                canImport={true}
              />;
      case 'Employs':
        return <Tableau 
                title="Employs" 
                donnees={donnees} 
                setDonnees={setdonnees}
                ComponentDetail={DetailEmployee} 
                detail={detail} 
                setDetailVisible={setDetail}
                canImport={false}
                />;
      case 'Chantiers':
        return <Tableau 
                title="Chantiers"
                donnees={donnees}
                setDonnees={setdonnees}
                ComponentDetail={DetailChantiers} 
                detail={detail} 
                setDetailVisible={setDetail}
                canImport={false}
              />;
      case 'Formations':
        return <Tableau
                title="Formation"
                donnees={donnees}
                setDonnees={setdonnees}
                ComponentDetail={DetailFormations}
                detail={detail}
                setDetailVisible={setDetail}
                canImport={false}
              />;
      case 'StatusTravail':
        return <Tableau
                title="Status de travail"
                donnees={donnees}
                setDonnees={setdonnees}
                ComponentDetail={DetailStatusTravail} 
                detail={detail} 
                setDetailVisible={setDetail}
                canImport={false}
              />;
      default:
        return null;
    }
  }

  return (
    <div className="App">
      <div style={{ 
          display: 'flex', 
          flexDirection: 'row',
          gap: '10px',
        }}
      >
        {!isLoggedIn ? (
          <Login setLoggedIn={setIsLoggedIn} />
        ) : (
        <>
          <SideBandeau onSelect={setSelectedTab} selected={selectedTab} setDonnees={setdonnees} setDetail={setDetail} />
          {renderRightComponent()}
        </>
        )}
      </div>
    </div>
  );
}

export default App;
