import React, { useState,useEffect } from 'react';
import styles from '../styles/Detail.module.css';

const Detail = ({ paramtrage,mode,onSave,onDelete,onAdd,onCancel }) => {
    const [values, setValues] = useState({});

    useEffect(() => {
        const initialValues = {};
        paramtrage.forEach(param => {
            if (param.type === 'input') {
                initialValues[param.label] = param.value
            }
            else if (param.type === 'select') {
                if (param.idx){
                    initialValues[param.label] = param.value[param.idx]?.id 
                }
                else {
                    initialValues[param.label] = param.value[0]?.id 
                }
            }
            else if (param.type === 'file') {
                initialValues[param.label] = param.value
            }
            else if (param.type === 'image') {
                initialValues[param.label] = param.value
            }
        });
        setValues(initialValues);
    }, [paramtrage]);

    const handleChange = (label, value) => {
        setValues((prevValues) => ({
            ...prevValues,
            [label]: value,
        }));
    };

    const handleSave = () => {
        onSave(values);
    };

    const handleAdd = () => {
        onAdd(values);
    }

    return (
        <div className={styles.card}>
            <div className={styles.title}>
                {mode === 'Ajout' ? 'Ajouter' : 'Modifier'}
            </div>
            {paramtrage.map((param) => (
                <div key={param.label} className={styles.container_label_input}>
                    <label className={styles.label}>
                        {param.label} :
                    </label>
                    {(() => {
                        switch (param.type) {
                            case 'input':
                                return (
                                    <input
                                        type="text"
                                        value={values[param.label] || ''}
                                        style={{ borderRadius: 6, border: '1px solid #ccc', padding: 5 }}
                                        onChange={(e) => handleChange(param.label, e.target.value)}
                                    />
                                );
                            case 'select':
                                return (
                                    <select
                                        className={styles.select}
                                        value={values[param.label] || ''}
                                        onChange={(e) => handleChange(param.label, e.target.value)}
                                    >
                                        {param.value.map((zone) => (
                                            <option key={zone.value} value={zone.value}>
                                                {zone.label}
                                            </option>
                                        ))}
                                    </select>
                                );
                            case 'file':
                                return (
                                    <div className={styles.fileContainer}>
                                        {values[param.label] ? (
                                            <a 
                                                href={typeof values[param.label] === 'string' ? values[param.label] : URL.createObjectURL(values[param.label])}
                                                target="_blank" 
                                                rel="noopener noreferrer"
                                                className={styles.imageLink}
                                            >
                                                Voir le fichier
                                            </a>
                                        ) : (
                                            null
                                        )}
                                        <div className={styles.labelInputContainer}>
                                            <label htmlFor={param.label} className={styles.fileLabel}>
                                                {values[param.label] ? "Choisir un autre fichier" : ' Slectionner un fichier'}
                                            </label>
                                            <input
                                                id={param.label}
                                                className={styles.input}
                                                type="file"
                                                onChange={(e) => handleChange(param.label, e.target.files[0])}
                                            />
                                        </div>
                                    </div>
                                );
                            case 'image':
                                return (
                                    <div className={styles.fileContainer}>
                                        {values[param.label]  ? (
                                            <img
                                                alt="img"
                                                src={typeof values[param.label] === 'string' ? values[param.label] : URL.createObjectURL(values[param.label])}
                                                style={{ width: 75, height: 75, borderRadius: 6 }} 
                                            />
                                        ) : (
                                            null
                                        )}
                                        <div className={styles.labelInputContainer}>
                                            <label htmlFor={param.label} className={styles.fileLabel}>
                                                {values[param.label] ? "Choisir une autre image" : 'Slectionner une image '}
                                            </label>
                                            <input
                                                id={param.label}
                                                className={styles.input}
                                                type="file"
                                                accept="image/*"
                                                onChange={(e) => handleChange(param.label, e.target.files[0])}
                                            />
                                        </div>
                                    </div>
                                );
                            default:
                                return <div>Caca</div>;
                        }
                    })()}
                </div>
            ))}
            {mode === 'Detail' && (
                <div className={styles.buttonContainer}>
                    <button className={styles.button} onClick={handleSave}>
                        Enregistrer
                    </button>
                    <button className={styles.button} style={{ backgroundColor: 'red' }} onClick={onDelete}>
                        Supprimer
                    </button>
                </div>
            ) }
            {mode === 'Ajout' && (
                <div className={styles.buttonContainer}>
                    <button className={styles.button} onClick={handleAdd}>
                        Valider
                    </button>
                    <button className={styles.button} style={{ backgroundColor: 'red' }} onClick={onCancel}>
                        Annuler
                    </button>
                </div>
            )}
        </div>
    );
};

export default Detail;
