import React,{useState,useMemo} from 'react';
import styles from '../styles/Tableau.module.css';

import config from '../Fonctions/config';

const Tableau = ({title,donnees,setDonnees,ComponentDetail,detail,setDetailVisible,canImport}) => {
    
    const [selectedRow, setSelectedRow] = useState(null);
    const [modeDetail, setModeDetail] = useState("Detail");
    const [sortConfig, setSortConfig] = useState({ key: null, direction: 'asc' });

    const sortedData = useMemo(() => {
        if (!donnees || donnees.length === 0) return [];

        const rowCount = donnees[0].contenu.length;
        const rows = Array.from({ length: rowCount }, (_, rowIndex) =>
            Object.fromEntries(donnees.map(d => [d.title, d.contenu[rowIndex]]))
        );

        if (sortConfig.key) {
            rows.sort((a, b) => {
                const aValue = a[sortConfig.key];
                const bValue = b[sortConfig.key];
                if (aValue < bValue) return sortConfig.direction === 'asc' ? -1 : 1;
                if (aValue > bValue) return sortConfig.direction === 'asc' ? 1 : -1;
                return 0;
            });
        }

        return rows;
    }, [donnees, sortConfig]);

    const handleSort = (key) => {
        setSortConfig(prev => {
            const direction = prev.key === key && prev.direction === 'asc' ? 'desc' : 'asc';
            return { key, direction };
        });
    };


    if (detail){
        return (
            <ComponentDetail 
                id={selectedRow.id} 
                setVisibility={setDetailVisible} 
                mode={modeDetail}
                setDonnees={setDonnees}
            />
        )
    }

return (
    <div className={styles.container}>
        <div className={styles.title}>
            {title}
        </div>
        <div className={styles.buttonContainer}>
            <button className={styles.button} onClick={() => {
                setDetailVisible(true);
                setModeDetail("Ajout");
                setSelectedRow(
                    donnees.reduce((acc, donnee) => {
                        acc[donnee.title] = '';
                        return acc;
                    }, {})
                );
            }}>
                Ajouter
            </button>
            {canImport &&
                <button className={styles.button} onClick={() => {
                    const input = document.createElement('input');
                    input.type = 'file';
                    input.accept = '.ods';
                    input.onchange = async (event) => {
                        const file = event.target.files[0];
                        const formdata = new FormData();
                        formdata.append('file', file);
                        const response = await fetch(config.URL+`/api/import/${title.toLowerCase()}/`, {
                            method: 'POST',
                            headers : {
                                'Authorization': `Bearer ${localStorage.getItem('access')}`,
                            },
                            body: formdata,
                        });
                        if (response.ok) {
                            const data = await response.json();
                            setDonnees((prev) => {
                                const newDonnees = [...prev];
                                const fieldMap = {
                                    "id": "id",
                                    "Ville": "villes",
                                    "Dept": "dept",
                                    "Zones": "zone",
                                    "Kms": "km"
                                };
                                newDonnees.forEach(item => {
                                    const key = fieldMap[item.title];
                                    if (key) {
                                        const newValues = data.zones.map(zone => zone[key]);
                                        item.contenu = item.contenu.concat(newValues);
                                    }
                                });
                                return newDonnees;
                            });
                        }
                    }
                    input.click();
                }
                }>
                    Importer
                </button>
            }
        </div>
        <div className={styles.tableWrapper}>
            <table className={styles.Tableau}>
                <thead>
                    <tr className={styles.title_tableau}>
                        {donnees.map((donnee, index) => (
                            <th key={index} onClick={() => handleSort(donnee.title)} className={styles.th}>
                                <span>
                                    {donnee.title}
                                </span>
                                <span className={styles.arrow}>
                                    {sortConfig.key === donnee.title ? (sortConfig.direction === 'asc' ? '' : '') : ''}
                                </span>
                            </th>
                        ))}
                    </tr>
                </thead>
                <tbody>
                    {sortedData.map((rowData, rowIndex) => (
                        <tr key={rowIndex} className={styles.row} onClick={() => {
                            setSelectedRow(rowData);
                            setDetailVisible(true);
                            setModeDetail("Detail");
                        }}>
                            {donnees.map((donnee, colIndex) => (
                                <td key={colIndex} className={styles.td}>
                                    {rowData[donnee.title]
                                }</td>
                            ))}
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
)};

export default Tableau;
