import styles from '../styles/Login.module.css';
import requets from '../Fonctions/requets';
import React, { useState } from 'react';
import { FaEye, FaEyeSlash } from 'react-icons/fa'; // Icones pour l'il ouvert et l'il ferm

const Login = ({setLoggedIn}) => {

    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [showPassword, setShowPassword] = useState(false);
    const [error, setError] = useState(null);

    const togglePasswordVisibility = () => {
        setShowPassword(!showPassword);
    }


    const handleLogin = async () => {
        setError(null);
        const isadmin = await requets.logIn(username, password);
        if (isadmin) {
            setLoggedIn(true);
            return
        }
        if (isadmin === null) {
            setError('Une erreur est survenue lors de la connexion');
            return;
        }
        setError('Nom d\'utilisateur ou mot de passe incorrect');
        
    }

    return (
        <div className={styles.container}>
            <div className={styles.box}>
                <div className={styles.title}>
                    Login
                </div>
                <div className={styles.formContent}>
                    <div className={styles.inputContainer}>
                        <label className={styles.label}>Nom d'utilisateur</label>
                        <input type="text" className={styles.input} placeholder="Nom d'utilisateur" onChange={(e) => setUsername(e.target.value)} />
                    </div>
                    <div className={styles.inputContainer}>
                        <label className={styles.label}>Mot de passe</label>
                        <div className={styles.inputWrapper}>
                            <input 
                                type={showPassword ? "text" : "password"}
                                className={styles.input} 
                                placeholder="Mot de passe" 
                                onChange={(e) => setPassword(e.target.value)} 
                            />
                            <div className={styles.eyeIcon} onClick={togglePasswordVisibility}>
                                {showPassword ? <FaEyeSlash /> : <FaEye />}
                            </div>
                        </div>
                    </div>
                </div>
                <button type="submit" onClick={handleLogin} className={styles.button}>
                    Login
                </button>
                {error && <div className={styles.error}>{error}</div>}
            </div>
        </div>
    );
}

export default Login;
