import Detail from '../Detail';
import React , { useState, useEffect } from 'react';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';

import {get} from '../../Fonctions/requets';
import config from '../../Fonctions/config';

const DetailFormations = ({ id,setVisibility,mode,setDonnees }) => {
    const [infos, setInfos] = useState({});
    const MySwal = withReactContent(Swal);

    useEffect(() => {
        const fetchData = async () => {
            const data = await get(config.URL+`/api/formations/${id}/`);
            if (!data) {
                setInfos({});
                return;
            }
            setInfos(data);
        }
        if (mode === "Ajout")
            return;
        fetchData();
    }, [id,mode]);

    const [paramtrage, setParamtrage] = useState([]);
    useEffect(() => {
        const isEdition = mode !== "Ajout";
        setParamtrage([
            { label: 'Nom', value: isEdition ? infos.name || '' : '', type: 'input' },
            { label: 'Fichier', value:isEdition ? infos.file || '' : '', type: 'file' },
        ]);
    }, [infos,mode]);

    const handleSave = async (values) => {
        if (!values['Nom']) {
            MySwal.fire({
                title: <strong>Le nom doit tre prsent</strong>,
                icon: 'warning',
            });
            return;
        }
        const formData = new FormData();
        formData.append('name', values['Nom']);
        if (values['Fichier'] && values['Fichier'] instanceof File) {
            formData.append('file', values['Fichier']);
        }
        try {
            const response = await fetch(config.URL+`/api/formations/${id}/`, {
                method: 'PATCH',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('access')}`,
                },
                body: formData,
            });
            if (response.ok) {
                const data = await response.json();
                setDonnees((prev) => {
                    const newDonnees = [...prev];
                    const index = newDonnees.findIndex(item => item.title === 'id');
                    if (index !== -1) {
                        newDonnees[index].contenu = newDonnees[index].contenu.map(item => item === id ? data.id : item);
                    }
                    const index2 = newDonnees.findIndex(item => item.title === 'Nom');
                    if (index2 !== -1) {
                        newDonnees[index2].contenu = newDonnees[index2].contenu.map(item => item === infos.name ? data.name : item);
                    }
                    return newDonnees;
                });

                MySwal.fire({
                    title: <strong>Formation modifie avec succs</strong>,
                    icon: 'success',
                });
            } else {
                MySwal.fire({
                    title: <strong>Erreur lors de la modification de la formation</strong>,
                    icon: 'error',
                });
            }
        } catch (error) {
            MySwal.fire({
                title: <strong>Erreur lors de la modification de la formation</strong>,
                icon: 'error',
            });
        }
        finally {
            setVisibility(false);
        }
    }

    const handleDelete = async () => {
        const result = await MySwal.fire({
            title: <strong>tes-vous sr de vouloir supprimer cette formation ?</strong>,
            html: <i>Cette action supprimera toutes les donnes lies.</i>,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Oui',
            cancelButtonText: 'Non',
        });
        if (result.isConfirmed) {
            try {
                const response = await fetch(config.URL+`/api/formations/${id}/`, {
                    method: 'DELETE',
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('access')}`,
                        'Content-Type': 'application/json',
                    },
                });
                if (response.ok) {
                    setDonnees((prev) => {
                        const newDonnees = [...prev];
                        const index = newDonnees.findIndex(item => item.title === 'id');
                        if (index !== -1) {
                            newDonnees[index].contenu = newDonnees[index].contenu.filter(item => item !== id);
                        }
                        return newDonnees;
                    });

                    MySwal.fire({
                        title: <strong>Formation supprime avec succs</strong>,
                        icon: 'success',
                    });
                } else {
                    MySwal.fire({
                        title: <strong>Erreur lors de la suppression</strong>,
                        icon: 'error',
                    });
                }
            } catch (error) {
                MySwal.fire({
                    title: <strong>Erreur lors de la suppression</strong>,
                    icon: 'error',
                });
            } finally {
                setVisibility(false);
            }
        }
        else {
            MySwal.fire({
                title: <strong>Suppression annule</strong>,
                icon: 'info',
            });
        }

    }

    const handleAdd = async (values) => {
        if (!values['Nom'] || !values['Fichier']) {
            MySwal.fire({
                title: <strong>Veuillez remplir tous les champs</strong>,
                icon: 'warning',
            });
            return;
        }
        const formData = new FormData();
        formData.append('name', values['Nom']);
        formData.append('file', values['Fichier']);

        try {
            const response = await fetch(config.URL+'/api/formations/', {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('access')}`,
                },
                body: formData,
            });
            if (response.ok) {
                const data = await response.json();
                setDonnees((prev) => {
                    const isExisting = prev.some(item => item.contenu.includes(data.id));
                    if (isExisting) {
                        return prev;
                    }
                    const newDonnees = [...prev];
                    const index = newDonnees.findIndex(item => item.title === 'id');
                    if (index !== -1) {
                        newDonnees[index].contenu = [...newDonnees[index].contenu, data.id];
                    }
                    const index2 = newDonnees.findIndex(item => item.title === 'Nom');
                    if (index2 !== -1) {
                        newDonnees[index2].contenu = [...newDonnees[index2].contenu, data.name];
                    }
                    return newDonnees;
                });
                MySwal.fire({
                    title: <strong>Formation ajoute avec succs</strong>,
                    icon: 'success',
                });
            } else {
                MySwal.fire({
                    title: <strong>Erreur lors de l'ajout de la formation</strong>,
                    icon: 'error',
                });
            }
        } catch (error) {
            MySwal.fire({
                title: <strong>Erreur lors de l'ajout de la formation</strong>,
                icon: 'error',
            });
        }
        finally {
            setVisibility(false);
        }
    }

    return (
        <div style={{ display: 'flex', flexDirection: 'column', gap: '10px',width:'100%',maxHeight:'100vh',overflow:'scroll',justifyContent:'center',alignItems:'center' }}>
            <Detail paramtrage={paramtrage} mode={mode} onSave={handleSave} onDelete={handleDelete} onAdd={handleAdd} onCancel={() => setVisibility(false)} />
        </div>
    );
}

export default DetailFormations;