import React from 'react';
import { FaTrash, FaDownload } from "react-icons/fa";

import styles from '../../styles/PhotoWithButton.module.css';

const PhotoWithButton = ({ photo, onDelete, alt, style,onDownload }) => {
    const downloadImage = (imageUrl, filename) => {
        const image = new Image();
        image.crossOrigin = "anonymous";
        image.src = imageUrl;
        image.onload = () => {
            const canvas = document.createElement("canvas");
            canvas.width = image.width;
            canvas.height = image.height;
            const ctx = canvas.getContext("2d");
            ctx.drawImage(image, 0, 0);
            canvas.toBlob((blob) => {
                const link = document.createElement("a");
                link.href = URL.createObjectURL(blob);
                link.download = filename || "downloaded_image.png";
                document.body.appendChild(link);
                link.click();
                document.body.removeChild(link);
            }, "image/png");
        };
        onDownload();
    };

    return (
        <div 
            className={styles.container} 
            onClick={(e) => {
                e.stopPropagation();
                downloadImage(photo, alt);
        }}>
            <div className={styles.overlay}>
                <button 
                    onClick={(e) => {
                        e.stopPropagation();
                        onDelete();
                    }}
                    className={styles.cross}
                    style={{
                        position: "absolute",
                        right : 3,
                        top: 3,
                        zIndex: 2,
                    }}
                >
                    <FaTrash style={{ color: "white", fontSize: 15 }} />
                </button>
                <FaDownload style={{ color: "white", fontSize: 50 }} />
            </div>
            <img src={photo} alt={alt} style={{...style }} />
        </div>
    );
}


export default PhotoWithButton;